<template>
  <div class="app-container" id="gunExps">
    <div class="filter-container">
      <el-form :inline="true" class="filterTool">
        <el-form-item style="width:150px">
          <el-input v-model="listQuery.iMei" placeholder="设备号/枪号" class="filter-item searchInput" />
        </el-form-item>
        <el-form-item class="dateRange">
          <el-date-picker class="filter-item" v-model="value1" type="daterange" range-separator="至" value-format="timestamp" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
        </el-form-item>

        <el-button class="filter-item" type="primary" @click="handleFilter" icon="el-icon-search">搜索</el-button>
        <el-button type="primary" @click="exportExcel" icon="iconfont icondaochu">导出</el-button>

      </el-form>

      <el-form :model="ruleForm" :inline="true" :rules="rules" ref="ruleForm" class="filterTool filterTool1">
        <el-form-item prop="value1" class="filter-item doexcute">
          <el-date-picker v-model="ruleForm.value1" type="date" placeholder="选择日期" value-format="yyyy_MM_dd" :picker-options="pickerOptions"></el-date-picker>
        </el-form-item>
        <el-button class="filter-item" type="primary" @click="excute">执行统计</el-button>
      </el-form>
    </div>
    <div class="tableBox">
      <el-table :key="tableKey" v-loading="listLoading" :data="list" v-show="list.length>0" fit highlight-current-row style="width: 100%;" :row-class-name="tableRowClassName">
        <el-table-column label="序号" fixed prop="id" sortable="custom" align="center" width="80" type="index" :index="indexMethod"></el-table-column>
        <el-table-column label="枪号" width="150px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.code }}</span>
          </template>
        </el-table-column>
        <el-table-column label="设备号" width="200px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.iMei }}</span>
          </template>
        </el-table-column>
        <el-table-column label="所属大队" width="150px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.teamName?row.teamName:'未分组' }}</span>
          </template>
        </el-table-column>
        <el-table-column label="前一次上报时间" minWidth="200px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.preTime }}</span>
          </template>
        </el-table-column>
        <el-table-column label="后一次上报时间" minWidth="200px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.nextTime }}</span>
          </template>
        </el-table-column>

        <el-table-column label="间隔(S)" width="200px" align="center">
          <template slot-scope="scope">
            <span>{{ secondsFormat(scope.row.intervalTime) }}</span>
          </template>
        </el-table-column>
        <el-table-column label="创建时间" width="200px" align="center">
          <template slot-scope="scope">
            <span>{{ formatDateTime(scope.row.createTime) }}</span>
          </template>
        </el-table-column>
        <el-table-column label="距离(米)" width="200px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.distance }}</span>
          </template>
        </el-table-column>

        <el-table-column label="操作" fixed="right" align="center" width="300" class-name="small-padding fixed-width" v-if="power==1">
          <template slot-scope="{row}">
            <el-button type="primary" size="mini" @click="playBack(row)">回放</el-button>
            <el-button type="danger" size="mini" @click="handleDelete(row)" icon="el-icon-delete">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <pagination v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="handleFilter('pagination')" layout="prev, pager, next, sizes, total, jumper" />
  </div>
</template>

<script>
import Pagination from "../../../components/Pagination"; // secondary package based on el-pagination
//异常列表，删除，搜索，执行统计
import {
  gunExpsList,
  gunExpsDelete,
  gunExpsSearch,
  gunExpsCount
} from "../../../api/managementArchives/gunExps";

import { formatDate } from "../../../../src/common";
import {
  exportExcelReq
} from "../../../../src/api/managementArchives/common";

let myLoading = undefined; // 弹窗服务调用

export default {
  name: "gunFiles",
  components: { Pagination },
  data() {
    return {
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now() - 24 * 60 * 60 * 1000; // 今天也不能选，因为今天的数据还未统计完全
        }
      },
      //执行统计的时间
      ruleForm: {
        value1: "" // 开始或者结束时间
      },
      //执行统计时间校验
      rules: {
        value1: [
          {
            type: "date",
            required: true,
            message: "请选择日期",
            trigger: "change"
          }
        ]
      },
      value1: [], //搜索的时间

      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      tableKey: 0,
      list: [], //异常列表
      total: 0, //异常数量
      indexMethod: 1, // 序号
      listLoading: false,
      listQuery: {
        page: 1,
        limit: 10,
        iMei: undefined
      },

      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [
          {
            text: "今天",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "昨天",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "一周前",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      }
    };
  },
  created() {
    //初始化获取所有异常列表
    this.handleFilter();
    // this.getList();
  },
  methods: {
    //获取时 分 秒
    time_to_sec(time) {
      var s = "";

      var hour = time.split(":")[0];
      var min = time.split(":")[1];
      var sec = time.split(":")[2];

      s = Number(hour * 3600) + Number(min * 60) + Number(sec);

      return s;
    },
    //转化为正常的需要传到历史回放页面的年月日时分秒格式
    sec_to_time(s) {
      var t;
      if (s > -1) {
        var hour = Math.floor(s / 3600);
        var min = Math.floor(s / 60) % 60;
        var sec = s % 60;
        if (hour < 10) {
          t = "0" + hour + ":";
        } else {
          t = hour + ":";
        }

        if (min < 10) {
          t += "0";
        }
        t += min + ":";
        if (sec < 10) {
          t += "0";
        }
        t += sec;
      }
      return t;
    },
    dealDate() {
      // 处理时间数组
      let _t = this;
      function formatTime(time) {
        // 格式时间
        let d = new Date(time),
          y = d.getFullYear(),
          m = d.getMonth() + 1,
          day = d.getDate();
        m = m < 10 ? "0" + m : m;
        day = day < 10 ? "0" + day : day;
        return `${y}_${m}_${day}`;
      }

      if (!_t.value1 || _t.value1.length == 0) {
        return [];
      }
      let oneDay = 1000 * 60 * 60 * 24, // 一天的毫秒数
        s = _t.value1[0], // 取小的值作为开始时间
        e = _t.value1[1] + oneDay - 1, // 大的值作为结束时间并且加上一天的毫秒
        len = Math.ceil((e - s) / oneDay), // 用于将日期遍历
        dayArr = []; // 传到后台的日期数组
      for (let i = 0; i < len; i++) {
        let time = formatTime(s + oneDay * i);
        dayArr.push(time);
      }
      return dayArr;
    },
    //回放功能
    playBack(row) {
      //回放规则是：回放时间由row.preTime这个时间往前和往后分别推1分钟
      //如果处理后的时间跨天了则开始时间默认为row.preTime这个时间的年月日，开始时间时分秒改成00:00:00，结束时间时分秒改为23:59:59
      let start = "00:00:00";
      let end = "23:59:59";
      let startInterval = this.time_to_sec(row.preTime.split(" ")[1]) - 60;
      let endInterval = this.time_to_sec(row.nextTime.split(" ")[1]) + 60;
      if (startInterval > 0) {
        start = this.sec_to_time(startInterval);
      }
      if (endInterval < 24 * 3600) {
        end = this.sec_to_time(endInterval);
      }
      this.$router.push({
        name: `historicalPlayback`,
        query: {
          imei: row.iMei,
          type: "gun",
          year: row.preTime.split(" ")[0],
          start: start,
          end: end
        }
      });
    },
    //格式化时间
    formatDateTime(timestamp) {
      if (!timestamp) {
        return timestamp;
      }
      return formatDate(timestamp, 1);
    },
    //格式化时间
    secondsFormat(s) {
      var day = Math.floor(s / (24 * 3600)); // Math.floor()向下取整
      var hour = Math.floor((s - day * 24 * 3600) / 3600);
      var minute = Math.floor((s - day * 24 * 3600 - hour * 3600) / 60);
      var second = s - day * 24 * 3600 - hour * 3600 - minute * 60;
      return day + "天" + hour + "时" + minute + "分" + second + "秒";
    },
    //执行统计
    excute() {
      // 查找数据
      let _t = this;
      _t.$refs.ruleForm.validate(valid => {
        if (valid) {
          if (!this.ruleForm.value1) {
            this.$message.warning("请选择日期");
            return;
          }
          gunExpsCount({
            tableName: this.ruleForm.value1
          })
            .then(res => {
              if (res.status == 0) {
                this.$message.success(res.msg);
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              _t.listLoading = false;
            });
        }
      });
    },
    //获取异常列表
    getList() {
      let _t = this;
      _t.listLoading = true;
      gunExpsList({
        page: this.listQuery.page,
        limit: this.listQuery.limit
      })
        .then(res => {
          if (res.status == 0) {
            this.listLoading = false;
            this.list = res.data;
            if (res.data.length == 0) {
              this.$message.success("无数据");
            }

            this.total = res.count;
            this.indexMethod = (_t.listQuery.page - 1) * _t.listQuery.limit + 1;
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          _t.listLoading = false;
        });
    },

    //搜索
    handleFilter(pagination) {
      if (pagination !== "pagination") {
        this.listQuery.page = 1;
      }
      this.listLoading = true;
      if (this.listQuery.iMei == undefined || this.listQuery.iMei == null) {
        this.listQuery.iMei = "";
      }
      if (!this.value1) {
        this.value1 = [];
      }
      gunExpsSearch({
        keyword: this.listQuery.iMei,
        tableName: this.dealDate(),
        page: this.listQuery.page,
        limit: this.listQuery.limit
      }).then(res => {
        if (res.status == 0) {
          this.total = 0;
          this.listLoading = false;
          if (res.data == null || res.data.length == 0) {
            this.list = [];
            this.$message.success("无数据");
          } else {
            this.list = res.data;
          }
          this.total = res.count;
          this.indexMethod =
            (this.listQuery.page - 1) * this.listQuery.limit + 1;
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    //删除
    handleDelete(row) {
      let _t = this;
      _t.listLoading = true;
      gunExpsDelete({
        id: row.id
      })
        .then(res => {
          if (res.status == 0) {
            if (_t.listQuery.page > 1 && _t.list.length <= 1) {
              // 最后一条返回上一页
              _t.listQuery.page -= 1;
            }
            this.handleFilter();
            _t.$message.success("刪除成功");
            this.$store.commit("realTimeMonitor/ISEDITFILES", true);
          } else {
            this.$message.error(res.msg);
            _t.listLoading = false;
          }
        })
        .catch(err => {
          _t.listLoading = false;
          console.error(err);
        });
    },

    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
    exportExcel() {
      let _t = this;
      // 查找数据
      let item = this.dealDate();
      exportExcelReq({ dates: item, excelType: 10, title: '枪支异常信息' }).then(res => {
        let disposition = res.headers;
        let endDate = "";
        if (item.length > 1) {
          endDate = item[item.length - 1];
        }
        let fileName = "";
        if (item.length == 0) {
          fileName = "枪支异常信息" + ".xls"
        } else {
          fileName = item[0] + "----" + endDate + "枪支异常信息" + ".xls";

        }
        let blob = new Blob([res], { type: "application/excel" }); // 假设文件为pdf
        let link = document.createElement("a");
        link.href = window.URL.createObjectURL(blob);
        link.download = fileName;
        link.click();
        link.remove();
      });


    },
  }
};
</script>
<style lang="scss">
#gunExps {
  .filter-container {
    .dateRange {
      .el-form-item,
      .el-input__inner {
        width: 262px;
      }
      &.el-form-item {
        width: 262px;
      }
      .el-date-editor .el-range-input {
        width: 40%;
      }
    }
    .doexcute {
      .el-form-item,
      .el-input__inner {
        width: 145px;
      }
      &.el-form-item {
        width: 145px;
      }
      .el-date-editor .el-range-input {
        width: 40%;
      }
    }

    .searchInput {
      width: 150px;
      .el-input__inner {
        width: 150px;
      }
      margin-right: 20px;
    }
    position: relative;
    line-height: initial;
    .el-input__inner {
      height: 30px;
      line-height: 30px;
    }
    .i,
    .el-range-separator {
      line-height: 24px;
      padding: 0;
      width: 6%;
    }
    .el-form-item__content {
      line-height: 30px;
    }
  }
  .el-date-editor .el-range__icon {
    line-height: 30px;
  }
  .el-form-item {
    margin: 0;
    width: auto;
  }
  .el-input__icon {
    height: 30px;
    line-height: 30px;
  }
}
.filterTool1 {
  position: absolute;
  right: 40px;
}
</style>




