<template>
  <div class="app-container" id="carMaintain">
    <div class="filter-container">
      <el-input
        v-model="listQuery.iMei"
        placeholder="车牌号/油卡号"
        class="filter-item"
        @keyup.enter.native="handleFilter"
      />
      <el-button class="filter-item" type="primary" @click="handleFilter" icon="el-icon-search">搜索</el-button>
      <el-button
        class="filter-item"
        type="primary"
        @click="handleCreate"
        v-if="power==1"
        icon="el-icon-circle-plus-outline"
      >添加</el-button>
       <el-button class="filter-item filter-item-btn" type="primary" @click="exportExcel" icon="iconfont icondaochu">导出</el-button>

    </div>
<div class="tableBox">

    <el-table
      :key="tableKey"
      v-loading="listLoading"
      :data="list"
      fit
      highlight-current-row
      style="width: 100%;"
      :row-class-name="tableRowClassName"
    >
      <el-table-column
        label="序号"
        fixed
        prop="id"
        sortable="custom"
        align="center"
        width="80"
        type="index"
        :index="indexMethod"
      ></el-table-column>
      <!-- <el-table-column label="id" width="100px" align="center">
        <template slot-scope="{row}">
          <span>{{ row.id }}</span>
        </template>
      </el-table-column> -->
      <!-- <el-table-column label="设备号" minWidth="200px" align="center">
        <template slot-scope="{row}">
          <span>{{ row.serialNo }}</span>
        </template>
      </el-table-column> -->
      <el-table-column label="车牌号" minWidth="200px" align="center">
        <template slot-scope="{row}">
          <span>{{ row.licenseNo }}</span>
        </template>
      </el-table-column>
      <el-table-column label="计划保养日期" minWidth="200px" align="center">
        <template slot-scope="{row}">
          <span>{{ formatDateTime(row.planMaintainTime )}}</span>
        </template>
      </el-table-column>
      <el-table-column label="计划保养里程" minWidth="200px" align="center">
        <template slot-scope="{row}">
          <span>{{ row.planMaintainMileage }}</span>
        </template>
      </el-table-column>
      <el-table-column label="推荐保养日期" minWidth="200px" align="center">
        <template slot-scope="{row}">
          <span>{{ formatDateTime(row.adviceMaintainTime) }}</span>
        </template>
      </el-table-column>
      <el-table-column label="在当前保养周期内已运行的里程" minWidth="300px" align="center">
        <template slot-scope="{row}">
          <span>{{ row.currentMileage }}</span>
        </template>
      </el-table-column>
      <el-table-column label="最后一次保养的时间" minWidth="200px" align="center">
        <template slot-scope="{row}">
          <span>{{ formatDateTime(row.latestMaintainTime) }}</span>
        </template>
      </el-table-column>
      <el-table-column label="所属大队" minWidth="200px" align="center">
        <template slot-scope="{row}">
          <span>{{ row.teamName==""?'未分组':row.teamName }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="操作"
        fixed="right"
        align="center"
        width="300"
        class-name="small-padding fixed-width"
        v-if="power==1"
      >
        <template slot-scope="{row}">
          <!-- <el-button type="primary" size="mini" class="pausebtn" @click="check(row)">保养</el-button> -->
          <el-button type="primary" size="mini" @click="handleUpdate(row)" v-if="power==1" icon="el-icon-edit-outline">编辑</el-button>
          <el-button type="danger" size="mini" @click="handleDelete(row)" v-if="power==1" icon="el-icon-delete">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
</div>

    <pagination
      v-show="total>0"
      :total="total"
      :page.sync="listQuery.page"
      :limit.sync="listQuery.limit"
      @pagination="getList"
      layout="prev, pager, next, sizes, total, jumper"

    />

    <el-dialog id="myDialog" :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible" center>
      <el-form
        ref="dataForm"
        :rules="rules"
        :model="temp"
        label-position="left"
        class="formStyle"
      >
        <el-form-item label="车牌号" prop="licenseNo" v-if="dialogStatus=='create'">
          <el-input v-model="temp.licenseNo" />
        </el-form-item>
        <!-- <el-form-item label="保养日期" prop="maintainTime" v-if="dialogStatus==='maintain'">
          <el-date-picker v-model="temp.maintainTime" type="datetime" placeholder="选择日期时间"></el-date-picker>
        </el-form-item> -->
        <el-form-item label="上次保养日期" prop="latestMaintainTime">
          <el-date-picker v-model="temp.latestMaintainTime" type="datetime" placeholder="选择日期时间"></el-date-picker>
        </el-form-item>
        <el-form-item label="计划保养里程" prop="planMaintainMileage">
          <el-input v-model="temp.planMaintainMileage" placeholder="单位：公里" />
        </el-form-item>

        <el-form-item label="计划保养日期" prop="planMaintainTime">
          <el-date-picker  v-model="temp.planMaintainTime" type="datetime" placeholder="选择日期时间" ></el-date-picker>
        </el-form-item>
        <el-form-item
          label="距离上次保养已行驶的里程"
          class="special"
          prop="currentMileage"
        >
          <el-input v-model="temp.currentMileage" placeholder="单位：公里" />
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button
          type="primary"
          @click="dialogStatus==='create'?createData():(dialogStatus==='update'?updateData():maintain())"
        >确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Pagination from "../../../components/Pagination"; // secondary package based on el-pagination
//保养列表，添加，编辑，搜索，添加保养计划
import {
  maintainList,
  addMaintain,
  updateMaintain,
  deleteMaintain,
  searchMaintain,
  maintainRecord
} from "../../../api/managementArchives/carMaintain";
import { formatDate } from "../../../../src/common";
import {
 
  exportExcelReq
} from "../../../api/managementArchives/common";

let myLoading = undefined; // 弹窗服务调用

export default {
  name: "carMaintain",
  components: { Pagination },
  data() {
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      tableKey: 0,
      list: null, //保养列表
      total: 0,
      indexMethod: 1, // 序号
      listLoading: false,
      listQuery: {
        page: 1,
        limit: 10,
        iMei: undefined
      },
      //添加，编辑，保养，功能需要提交的数据
      temp: {
        planMaintainTime: undefined,
        licenseNo: undefined,
        planMaintainMileage: undefined,
        currentMileage: undefined,
        maintainTime: undefined,
        latestMaintainTime: undefined
      },
      dialogFormVisible: false, //窗口显隐

      dialogStatus: "", //控制是编辑窗口还是添加窗口
      textMap: {
        update: "编辑",
        create: "添加",
        maintain: "保养"
      },
      //表单校验
      rules: {
        licenseNo: [
          { required: true, message: "请填写车牌号", trigger: "blur" }
        ],
        latestMaintainTime: [
          { required: true, message: "请选择上次保养日期", trigger: "blur" }
        ],
        maintainTime: [
          { required: true, message: "请选择保养日期", trigger: "blur" }
        ],
        planMaintainTime: [
          { required: true, message: "请选择计划保养日期", trigger: "blur" }
        ],
        planMaintainMileage: [
          { required: true, message: "请填写计划保养里程", trigger: "blur" }
        ],
        currentMileage: [
          {
            required: true,
            message: "请填写距离上次保养已行驶的里程",
            trigger: "blur"
          }
        ]
      }
    };
  },
  created() {
    //保养列表获取
    this.getList();
  },
  methods: {
    //格式化时间
    formatDateTime(timestamp) {
      if (!timestamp) {
        return timestamp;
      }
      //下面第二个参数传1代表时间会显示出时分秒，不传或者其他的都只显示年月日
      return formatDate(timestamp, 1);
    },
    //保养列表获取
    getList() {
      let _t = this;
      _t.listLoading = true;
      maintainList({
        page: _t.listQuery.page,
        limit: _t.listQuery.limit
      })
        .then(res => {
          if (res.status == 0) {
            this.listLoading = false;
            if (res.data == null) {
              this.list = [];
            } else {
              this.list = res.data;
            }

            this.total = res.count;
            this.indexMethod = (_t.listQuery.page - 1) * _t.listQuery.limit + 1;
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          _t.listLoading = false;
        });
    },
    //点击搜索
    handleFilter() {
      if (this.listQuery.iMei == "" || this.listQuery.iMei == undefined) {
        this.listQuery.iMei = null;
        this.getList();
        return;
      }
      searchMaintain({
        keyword: this.listQuery.iMei
      }).then(res => {
        if (res.status == 0) {
          this.listQuery.page = 1;
          this.listLoading = false;
          this.list = [];
          this.list = res.data;
          this.total = 0;
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    //重置
    resetTemp() {
      this.temp = {
        planMaintainTime: undefined,
        licenseNo: undefined,
        planMaintainMileage: undefined,
        currentMileage: undefined,
        maintainTime: undefined,
        latestMaintainTime: undefined
      };
    },
    //点击添加
    handleCreate() {
      this.resetTemp();
      this.dialogStatus = "create";
      this.dialogFormVisible = true;

      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },
    //添加
    createData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          addMaintain({
            planMaintainTime: this.temp.planMaintainTime.getTime(),
            licenseNo: this.temp.licenseNo,
            planMaintainMileage: Number(this.temp.planMaintainMileage),
            currentMileage: Number(this.temp.currentMileage),
            latestMaintainTime: this.temp.latestMaintainTime.getTime()
          })
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success(res.msg);
                this.dialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },
    //点击编辑
    handleUpdate(row) {
      this.temp = {
        latestMaintainTime:formatDate(row.latestMaintainTime,1),
        planMaintainTime:formatDate(row.planMaintainTime,1),

        planMaintainMileage:row.planMaintainMileage,

        currentMileage:row.currentMileage,
        id:row.id
      }
      this.dialogStatus = "update";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },
    //编辑
    updateData() {
      let that = this;
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          updateMaintain({
            planMaintainTime: new Date(that.temp.planMaintainTime).getTime(),
            planMaintainMileage: Number(that.temp.planMaintainMileage),
            id: this.temp.id,
            currentMileage: Number(that.temp.currentMileage),
            latestMaintainTime: new Date(that.temp.latestMaintainTime).getTime()
          })
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success(res.msg);
                this.dialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },
    //删除
    handleDelete(row) {
      let _t = this;
      _t.listLoading = true;
      deleteMaintain({
        id: row.id
      })
        .then(res => {
          if (res.status == 0) {
            if (_t.listQuery.page > 1 && _t.list.length <= 1) {
              // 最后一条返回上一页
              _t.listQuery.page -= 1;
            }
            this.getList();
            _t.$message.success("刪除成功");
          } else {
            this.$message.error(res.msg);
            _t.listLoading = false;
          }
        })
        .catch(err => {
          _t.listLoading = false;
        });
    },

    //点击保养
    check(row) {
      this.resetTemp();
      this.temp.licenseNo = row.licenseNo;
      this.dialogStatus = "maintain";
      this.dialogFormVisible = true;

      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },
    //添加一个保养计划
    maintain(row) {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          maintainRecord({
            maintainTime: this.temp.maintainTime.getTime(),
            licenseNo: this.temp.licenseNo,
            newPlanMaintainTime: this.temp.planMaintainTime.getTime(),
            newPlanMaintainMileage: Number(this.temp.planMaintainMileage)
          })
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success(res.msg);
                this.dialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
     exportExcel() {  
      let _t = this;
      // 查找数据
      exportExcelReq({ excelType: 15,title:'车辆保养记录' }).then(res => {
         try {
              let enc = new TextDecoder('utf-8')
              let data = JSON.parse(enc.decode(new Uint8Array(res)))
              this.$message({
                message: data.msg,
                type: 'warning'
              });

            } catch (error) {
        let disposition = res.headers;
        let fileName = "车辆保养记录" + ".xls";
        let blob = new Blob([res], { type: "application/excel" }); // 假设文件为pdf
        let link = document.createElement("a");
        link.href = window.URL.createObjectURL(blob);
        link.download = fileName;
        link.click();
        link.remove();
            }
      });
    }
  }
};
</script>
<style lang="scss">
#carMaintain {
  .el-form-item__label {
    width: 147px !important;
  }
  .el-form-item__content {
    margin-left: 147px !important;
    width:277px;
  }
  .special .el-form-item__label{
    line-height: 30px;
  }
}
</style>




