import request from '../../request'
//异常列表
export function gunExpsList(data) {
    return request({
        url: '/gunExps/page',
        method: 'post',
        data
    })
}
//异常删除
export function gunExpsDelete(data) {
    return request({
        url: '/gunExps/delete',
        method: 'post',
        data
    })
}
//异常查询
export function gunExpsSearch(data) {
    return request({
        url: '/gunExps/fuzzyQuery',
        method: 'post',
        data
    })
}

//执行统计
export function gunExpsCount(data) {
    return request({
        url: '/gunExps/count',
        method: 'post',
        data
    })
}