import request from '../../request'
//异常列表
export function carExpsList(data) {
    return request({
        url: '/carExps/page',
        method: 'post',
        data
    })
}
//异常删除
export function carExpsDelete(data) {
    return request({
        url: '/carExps/delete',
        method: 'post',
        data
    })
}
//异常查询
export function carExpsSearch(data) {
    return request({
        url: '/carExps/fuzzyQuery',
        method: 'post',
        data
    })
}

//执行统计
export function carExpsCount(data) {
    return request({
        url: '/carExps/count',
        method: 'post',
        data
    })
}