<template>
  <div class="app-container" id="oilStatistics">
    <div class="filter-container">
      <el-date-picker
        class="filter-item dateRange"
        v-model="timeRange"
        type="datetimerange"
        :picker-options="pickerOptions"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
        align="right"
      ></el-date-picker>

      <el-button class="filter-item" type="primary" @click="handleFilter" icon="el-icon-search">搜索</el-button>
      <el-button type="primary" @click="exportExcel" icon="iconfont icondaochu">导出</el-button>
    </div>
    <div class="tableBox">
      <el-table
        @sort-change="tableSortChange"
        :default-sort="{prop: 'totalRefuel', order: 'descending'}"
        :data="list.slice((currpage - 1) * pagesize, currpage * pagesize)"
        :key="tableKey"
        v-loading="listLoading"
        fit
        highlight-current-row
        style="width: 100%;"
        :row-class-name="tableRowClassName"
      >
        <el-table-column
          label="序号"
          fixed
          prop="id"
          sortable="custom"
          align="center"
          width="80"
          type="index"
          :index="indexMethod"
        ></el-table-column>

        <el-table-column label="油卡号" width="200px" align="center">
          <template slot-scope="{row}">
            <span v-if="power==1" class="link-type">{{ row.fuelcardNo }}</span>
            <span v-else>{{ row.fuelcardNo }}</span>
          </template>
        </el-table-column>
        <el-table-column label="车牌号" minWidth="200px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.licenseNo }}</span>
          </template>
        </el-table-column>

        <el-table-column sortable prop="totalRefuel" label="加油量总计" minWidth="200px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.totalRefuel }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="totalPrice" sortable label="加油金额总计" minWidth="200px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.totalPrice }}</span>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <el-pagination
      v-show="total>0"
      background
      layout="prev, pager, next, sizes, total, jumper"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="pagesize"
      :current-page="currpage"
      :total="list.length"
      @current-change="handleCurrentChange"
      @size-change="handleSizeChange"
    ></el-pagination>
  </div>
</template>

<script>
import Pagination from "../../../components/Pagination"; // secondary package based on el-pagination
//油量统计列表
import { oilStatisticsList } from "../../../api/managementArchives/oilManagement";
import { formatDate } from "../../../../src/common"; //格式化时间
import { exportExcelReq } from "../../../../src/api/managementArchives/common";
let myLoading = undefined; // 弹窗服务调用

export default {
  name: "oilStatistics",
  components: { Pagination },
  data() {
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      tableKey: 0,
      list: [], //排序后的list
      list1: [], //初始请求回来没有排序的list
      total: 0, //油量统计总条数
      currpage: 1, //当前页
      pagesize: 10, //页条数
      indexMethod: 1, // 序号
      listLoading: false,
      listQuery: {
        page: 1,
        limit: 10,
        iMei: undefined
      },
      //排序 排序值和排序属性
      sort: {
        order: undefined,
        prop: undefined
      },

      textMap: {
        update: "编辑",
        create: "添加"
      },
      //校验
      rules: {
        fuelcardNo: [
          { required: true, message: "请填写油卡号", trigger: "blur" }
        ],
        licenseNo: [
          { required: true, message: "请填写车牌号", trigger: "blur" }
        ]
      },
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      //默认的时间范围为一周
      timeRange: [
        new Date().setTime(new Date().getTime() - 3600 * 1000 * 24 * 7),
        new Date()
      ]
    };
  },
  created() {
    //获取油量统计列表
    this.getList();
  },
  methods: {
    //格式化时间
    formatDateTime(timestamp) {
      //下面第二个参数传1代表时间会显示出时分秒，不传或者其他的都只显示年月日
      return formatDate(timestamp, 1);
    },
    //获取油量统计列表
    getList() {
      let _t = this;
      _t.listLoading = true;
      oilStatisticsList({
        endTime: new Date(this.timeRange[1]).getTime(),
        startTime: new Date(this.timeRange[0]).getTime()
      })
        .then(res => {
          if (res.status == 0) {
            this.listLoading = false;
            if (res.data == null) {
              this.list = [];
              this.list1 = [];
            } else {
              this.list = res.data;
              this.list1 = [...res.data];
            }
            this.sortChange();
            this.total = this.list.length;
            this.indexMethod = (_t.currpage - 1) * _t.pagesize + 1;
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          _t.listLoading = false;
        });
    },
    //当点击页码，改变每页条数时都需要重新对list排序，不然内容错误
    sortChange() {
      if (this.sort.order === "descending") {
        //降序
        this.list = this.list.sort(this.sortBy(this.sort.prop, false));
      } else if (this.sort.order === "ascending") {
        //升序
        this.list = this.list.sort(this.sortBy(this.sort.prop, true));
      } else {
        //如果不排序则恢复到原来的内容
        this.list = [...this.list1];
      }
    },
    //点击升序或者降序时触发
    tableSortChange(column) {
      this.sort.order = column.order; //升序还是降序
      this.sort.prop = column.prop; //属性
      this.currpage = 1;
      if (column.order === "descending") {
        this.list = this.list.sort(this.sortBy(column.prop, false));
      } else if (column.order === "ascending") {
        this.list = this.list.sort(this.sortBy(column.prop, true));
      } else {
        this.list = [...this.list1];
      }
    },
    sortBy(attr, rev) {
      //第二个参数没有传递 默认升序排列
      if (rev == undefined) {
        rev = 1;
      } else {
        rev = rev ? 1 : -1;
      }

      return function(a, b) {
        a = a[attr];
        b = b[attr];
        if (a < b) {
          return rev * -1;
        }
        if (a > b) {
          return rev * 1;
        }
        return 0;
      };
    },
    //点击搜索
    handleFilter() {
      if (this.listQuery.iMei == "" || this.listQuery.iMei == undefined) {
        this.listQuery.iMei = null;
        this.getList();
        return;
      }
      oilCarSearch({
        keyword: this.listQuery.iMei
      }).then(res => {
        if (res.status == 0) {
          this.listQuery.page = 1;
          this.listLoading = false;
          this.list = [];
          this.list = res.data;
          this.total = 1;
        } else {
          this.$message.error(res.msg);
        }
      });
    },

    //页码发生改变时
    handleCurrentChange(cpage) {
      //需要重新排序
      this.sortChange();

      this.currpage = cpage;
      this.indexMethod = (this.currpage - 1) * this.pagesize + 1;
    },
    //改变每页显示条数时需要重新排序
    handleSizeChange(psize) {
      this.sortChange();

      this.pagesize = psize;
      this.indexMethod = (this.currpage - 1) * this.pagesize + 1;
    },

    scrollTop() {
      // 弹窗滚动到顶部
      let myDialog = document.querySelector("#myDialog .el-dialog__body");
      myDialog.scrollTop = 0;
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
    exportExcel() {
      let _t = this;
      // 查找数据

      let item = [];
      if (this.timeRange && this.timeRange.length) {
        item[0] = new Date(this.timeRange[0]).getTime();
        item[1] = new Date(this.timeRange[1]).getTime();
      }

      exportExcelReq({ dates: item, excelType:14,title:'加油记录统计' }).then(res => {
         try {
              let enc = new TextDecoder('utf-8')
              let data = JSON.parse(enc.decode(new Uint8Array(res)))
              this.$message({
                message: data.msg,
                type: 'warning'
              });

            } catch (error) {
        let disposition = res.headers;
        let fileName = "";
        if (item.length == 0) {
          fileName = "加油记录统计" + ".xls";
        } else {
          fileName = formatDate(this.timeRange[0],1) + "----" + formatDate(this.timeRange[1],1) + "加油记录统计" + ".xls";
        }
        let blob = new Blob([res], { type: "application/excel" }); // 假设文件为pdf
        let link = document.createElement("a");
        link.href = window.URL.createObjectURL(blob);
        link.download = fileName;
        link.click();
        link.remove();
            }
      });
    }
  }
};
</script>
<style lang="scss">
#oilStatistics {
  //  .filter-item{
  //    margin-right:40px;
  //  }
  //   .dateRange{
  //      margin-right:20px;
  //     &.el-input__inner{
  //     width:400px!important;
  //     }
  //     .el-range-input{
  //       background-color: rgba(249, 249, 249, 1);
  //     }
  //   }
  //   .el-date-editor--datetimerange.el-input__inner i{
  //     line-height: 26px;
  //   }
  .filter-container {
    .el-input__inner {
      width: 346px;
      margin-right: 40px;
    }
    .el-range-separator {
      padding: 0;
      line-height: 24px;
    }
    .el-date-editor .el-range-input {
      width: 60%;
      background-color: #fafafa !important;
    }
    .el-date-editor--datetimerange.el-input__inner i {
      line-height: 24px;
    }
  }
}
</style>




