<template>
  <div class="app-container" id="oilRecord">
    <div class="filter-container">
      <el-date-picker
        class="filter-item"
        v-model="timeRange"
        type="datetimerange"
        :picker-options="pickerOptions"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
        align="right"
      ></el-date-picker>
      <el-button class="filter-item" type="primary"  @click="handleFilter" icon="el-icon-search">搜索</el-button>
      <el-button class="filter-item" type="primary" @click="handleCreate" style="width:165px">添加加油记录</el-button>
      <el-button type="primary" @click="exportExcel(0)" icon="iconfont icondaochu">导出</el-button>
       <el-button type="primary" @click="exportExcel(1)" icon="iconfont icondaochu">导出所有</el-button>
      <el-button class="filter-item" type="primary" @click="goBack()">返回</el-button>
    </div>
    <div class="tableBox">
      <el-table
        :key="tableKey"
        v-loading="listLoading"
        :data="list"
        fit
        highlight-current-row
        style="width: 100%;"
        :row-class-name="tableRowClassName"
        layout="prev, pager, next, sizes, total, jumper"
      >
        <el-table-column
          label="序号"
          fixed
          prop="id"
          sortable="custom"
          align="center"
          width="80"
          type="index"
          :index="indexMethod"
        ></el-table-column>
        <el-table-column label="id" width="100px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.id }}</span>
          </template>
        </el-table-column>
        <el-table-column label="油卡号" width="200px" align="center">
          <template slot-scope="{row}">
            <span v-if="power==1" class="link-type" @click="handleUpdate(row)">{{ row.fuelcardNo }}</span>
            <span v-else>{{ row.fuelcardNo }}</span>
          </template>
        </el-table-column>
        <el-table-column label="车牌号" minWidth="200px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.licenseNo }}</span>
          </template>
        </el-table-column>
        <el-table-column label="加油量(L)" minWidth="200px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.refuel }}</span>
          </template>
        </el-table-column>
        <el-table-column label="加油金额(元)" minWidth="200px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.price }}</span>
          </template>
        </el-table-column>
        <el-table-column label="加油时间" minWidth="200px" align="center">
          <template slot-scope="{row}">
            <span>{{ formatDateTime(row.refuelTime) }}</span>
          </template>
        </el-table-column>

        <el-table-column
          label="操作"
          fixed="right"
          align="center"
          width="300"
          class-name="small-padding fixed-width"
        >
          <template slot-scope="{row}">
            <el-button type="primary" size="mini" @click="handleUpdate(row)" icon="el-icon-edit-outline">编辑</el-button>
            <el-button type="danger" size="mini" @click="handleDelete(row)" icon="el-icon-delete">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <pagination
      v-show="total>0"
      :total="total"
      :page.sync="listQuery.page"
      :limit.sync="listQuery.limit"
      @pagination="getList"
    />

    <el-dialog id="myDialog" :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible" center>
      <el-form
        ref="dataForm"
        :rules="rules"
        :model="temp"
        label-position="left"
      >
        <el-form-item label="加油量" prop="refuel">
          <el-input v-model="temp.refuel" placeholder="单位:(L)" />
        </el-form-item>
        <el-form-item label="加油金额" prop="price">
          <el-input v-model="temp.price" placeholder="单位:(元)" />
        </el-form-item>
        <el-form-item label="加油时间" prop="time" v-if="dialogStatus==='create'">
          <el-date-picker v-model="temp.time" type="datetime" placeholder="选择日期时间"></el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogStatus==='create'?createData():updateData()">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Pagination from "../../../components/Pagination"; // secondary package based on el-pagination
//加油记录列表，添加，编辑，删除
import {
  oilRecordList,
  addOilRecord,
  updateOilRecord,
  deleteOilRecord
} from "../../../api/managementArchives/oilManagement";
import { formatDate } from "../../../../src/common"; //格式化时间
import { exportExcelReq } from "../../../../src/api/managementArchives/common";


let myLoading = undefined; // 弹窗服务调用

export default {
  name: "oilRecord",
  components: { Pagination },
  data() {
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      tableKey: 0,
      list: null, //油卡记录列表数据
      total: 0, //油卡记录总数
      indexMethod: 1, // 序号
      listLoading: false,
      listQuery: {
        page: 1,
        limit: 10,
        iMei: undefined
      },
      //添加或编辑油卡记录需提交的内容
      temp: {
        id: undefined,
        refuel: undefined,
        price: undefined,
        time: undefined
      },
      fuelcardNo: this.$route.params.fuelcardNo, //油卡记录对应的油卡号（上个页面传过来）
      licenseNo: this.$route.params.licenseNo, //油卡记录对应的车牌号（上个页面传过来）
      startTime: null, //开始时间
      endTime: null, //结束时间
      dialogFormVisible: false, //弹窗显隐

      dialogStatus: "", //控制是编辑窗口还是添加窗口
      textMap: {
        update: "编辑",
        create: "添加"
      },
      //表单校验
      rules: {
        fuelcardNo: [
          { required: true, message: "请填写油卡号", trigger: "blur" }
        ],
        refuel: [{ required: true, message: "请填写加油量", trigger: "blur" }],
        price: [{ required: true, message: "请填写加油金额", trigger: "blur" }],
        time: [{ required: true, message: "请选择加油时间", trigger: "blur" }]
      },
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      //从时间组件获取的时间范围
      timeRange: [
        new Date().setTime(new Date().getTime() - 3600 * 1000 * 24 * 7),
        new Date()
      ]
    };
  },
  created() {
    //从时间组件获取默认的开始时间和结束时间
    this.startTime = new Date(this.timeRange[0]).getTime();
    this.endTime = new Date(this.timeRange[1]).getTime();
    this.getList(); //获取加油记录
  },
  methods: {
    //格式化时间
    formatDateTime(timestamp) {
      //下面第二个参数传1代表时间会显示出时分秒，不传或者其他的都只显示年月日
      return formatDate(timestamp, 1);
    },
    //把时间重置为一周内
    reset() {
      this.timeRange = [
        new Date().setTime(new Date().getTime() - 3600 * 1000 * 24 * 7),
        new Date()
      ];
      this.startTime = new Date(this.timeRange[0]).getTime();
      this.endTime = new Date(this.timeRange[1]).getTime();
    },
    //获取加油记录
    getList() {
      let _t = this;
      _t.listLoading = true;
      oilRecordList({
        startTime: this.startTime,
        endTime: this.endTime,
        fuelcardNo: this.fuelcardNo,
        licenseNo: this.licenseNo,
        page: this.listQuery.page,
        limit: this.listQuery.limit
      })
        .then(res => {
          if (res.status == 0) {
            this.listLoading = false;
            if (res.data == null) {
              this.list = [];
            } else {
              this.list = res.data;
            }
            this.total = res.count;
            this.indexMethod = (_t.listQuery.page - 1) * _t.listQuery.limit + 1;
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          _t.listLoading = false;
        });
    },
    //点击搜索
    handleFilter() {
      this.listQuery.page = 1;
      this.getList();
    },
    //重置
    resetTemp() {
      this.temp = {
        id: undefined,
        refuel: undefined,
        price: undefined,
        time: undefined
      };
    },
    //点击添加
    handleCreate() {
      this.resetTemp();
      this.dialogStatus = "create";
      this.dialogFormVisible = true;

      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },
    //添加
    createData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          addOilRecord({
            fuelcardNo: this.fuelcardNo,
            refuel: Number(this.temp.refuel),
            price: Number(this.temp.price),
            time: this.temp.time.getTime()
          })
            .then(res => {
              if (res.status == 0) {
                this.reset();
                this.getList();
                this.$message.success("添加成功");
                this.dialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },
    //点击编辑
    handleUpdate(row) {
      this.temp = Object.assign({}, row); // copy obj
      this.dialogStatus = "update";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },
    //编辑
    updateData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          updateOilRecord({
            id: this.temp.id,
            refuel: this.temp.refuel,
            price: this.temp.price
          })
            .then(res => {
              if (res.status == 0) {
                this.reset();
                this.getList();
                this.$message.success("编辑成功");
                this.dialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },
    //删除
    handleDelete(row) {
      let _t = this;
      _t.listLoading = true;
      deleteOilRecord({
        id: row.id
      })
        .then(res => {
          if (res.status == 0) {
            if (_t.listQuery.page > 1 && _t.list.length <= 1) {
              // 最后一条返回上一页
              _t.listQuery.page -= 1;
            }
            this.reset();
            this.getList();
            _t.$message.success("刪除成功");
          } else {
            this.$message.error(res.msg);
            _t.listLoading = false;
          }
        })

        .catch(err => {
          _t.listLoading = false;
        });
    },

    //返回上一页
    goBack() {
      window.history.back(-1);
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
     exportExcel(type) {
      let _t = this;
      // 查找数据

      let item = [];
      if (this.timeRange && this.timeRange.length) {
        item[0] = new Date(this.timeRange[0]).getTime();
        item[1] = new Date(this.timeRange[1]).getTime();
      }
      let keyword=""
      if(type==1){
      keyword=""
      }else{
      keyword=this.licenseNo
      }

      exportExcelReq({ dates: item, excelType:13,title:'加油记录',licenseNo:keyword}).then(res => {
         try {
              let enc = new TextDecoder('utf-8')
              let data = JSON.parse(enc.decode(new Uint8Array(res)))
              this.$message({
                message: data.msg,
                type: 'warning'
              });

            } catch (error) {
        let disposition = res.headers;
        let fileName = "";
         if(keyword){
          fileName=keyword+"----"
        }
        if (item.length == 0) {
          fileName += "加油记录" + ".xls";
        } else {
          fileName += formatDate(this.timeRange[0],1) + "----" + formatDate(this.timeRange[1],1) + " 加油记录" + ".xls";
        }
       
        let blob = new Blob([res], { type: "application/excel" }); // 假设文件为pdf
        let link = document.createElement("a");
        link.href = window.URL.createObjectURL(blob);
        link.download = fileName;
        link.click();
        link.remove();
            }
      });
    }
  }
};
</script>
<style lang="scss">
#oilRecord {
  .filter-container {
    .el-input__inner {
      width: 346px;
      margin-right: 40px;
    }
    .el-range-separator {
      padding: 0;
      line-height: 24px;
    }
    .el-date-editor .el-range-input {
      width: 60%;
      background-color: #fafafa !important;
    }
    .el-date-editor--datetimerange.el-input__inner i {
      line-height: 24px;
    }
  }

  .el-button--mini {
    padding-left: 4px;
    padding-right: 4px;
  }
}
</style>




